"use client"
import React, { useState, useMemo } from "react"
import { PlusIcon, PencilIcon, TrashIcon, ChevronUpIcon, ChevronDownIcon } from "@heroicons/react/24/solid"
import { MagnifyingGlassIcon } from "@heroicons/react/24/outline"


// --- TYPES AND MOCK DATA ---

type User = {
  id: string
  avatar: string
  firstName: string
  lastName: string
  email: string
  phone: string
  role: 'Admin' | 'User' | 'Editor'
  status: 'Active' | 'Pending' | 'Banned'
  emailVerified: boolean
  lastLogin: string // ISO date string
  createdAt: string // ISO date string
  totalSpent: number
  credits: number
}

const mockUsers: User[] = [
  { id: 'USR001', avatar: 'https://i.pravatar.cc/150?u=a042581f4e29026704d', firstName: 'Tony', lastName: 'Stark', email: 'tony@stark.com', phone: '555-0101', role: 'Admin', status: 'Active', emailVerified: true, lastLogin: '2023-10-26T10:00:00Z', createdAt: '2022-01-15T09:30:00Z', totalSpent: 5430.50, credits: 1500 },
  { id: 'USR002', avatar: 'https://i.pravatar.cc/150?u=a042581f4e29026704e', firstName: 'Bruce', lastName: 'Wayne', email: 'bruce@wayne.com', phone: '555-0102', role: 'User', status: 'Active', emailVerified: true, lastLogin: '2023-10-25T18:45:00Z', createdAt: '2022-03-20T11:00:00Z', totalSpent: 1250.00, credits: 200 },
  { id: 'USR003', avatar: 'https://i.pravatar.cc/150?u=a042581f4e29026704f', firstName: 'Peter', lastName: 'Parker', email: 'peter@dailybugle.com', phone: '555-0103', role: 'Editor', status: 'Pending', emailVerified: false, lastLogin: '2023-09-10T12:10:00Z', createdAt: '2023-08-01T14:00:00Z', totalSpent: 150.75, credits: 50 },
  { id: 'USR004', avatar: 'https://i.pravatar.cc/150?u=a042581f4e29026704a', firstName: 'Diana', lastName: 'Prince', email: 'diana@themyscira.com', phone: '555-0104', role: 'User', status: 'Banned', emailVerified: true, lastLogin: '2023-05-30T08:20:00Z', createdAt: '2022-02-10T18:00:00Z', totalSpent: 2800.00, credits: 0 },
  { id: 'USR005', avatar: 'https://i.pravatar.cc/150?u=a042581f4e29026704b', firstName: 'Clark', lastName: 'Kent', email: 'clark@dailyplanet.com', phone: '555-0105', role: 'Editor', status: 'Active', emailVerified: true, lastLogin: '2023-10-26T09:00:00Z', createdAt: '2021-11-05T10:00:00Z', totalSpent: 890.25, credits: 300 },
];

// --- HELPER COMPONENTS ---

const StatusBadge = ({ status }: { status: User['status'] }) => {
  const styles = {
    Active: 'bg-green-100 text-green-800',
    Pending: 'bg-yellow-100 text-yellow-800',
    Banned: 'bg-red-100 text-red-800',
  }
  return <span className={`px-2 py-1 rounded-full text-xs font-medium ${styles[status]}`}>{status}</span>
}

const RoleBadge = ({ role }: { role: User['role'] }) => {
    const styles = {
      Admin: 'bg-purple-100 text-purple-800',
      User: 'bg-blue-100 text-blue-800',
      Editor: 'bg-indigo-100 text-indigo-800',
    }
    return <span className={`px-2 py-1 rounded-full text-xs font-medium ${styles[role]}`}>{role}</span>
}

const BooleanIndicator = ({ value }: { value: boolean }) => (
  <div className={`h-3 w-3 rounded-full ${value ? 'bg-green-500' : 'bg-red-500'}`}></div>
);


// --- MAIN PAGE COMPONENT ---

export default function UsersPage() {
  const [users, setUsers] = useState(mockUsers)
  const [searchTerm, setSearchTerm] = useState("")
  const [sortConfig, setSortConfig] = useState<{ key: keyof User; direction: 'asc' | 'desc' } | null>(null);

  const filteredAndSortedUsers = useMemo(() => {
    let sortedUsers = [...users];

    if (sortConfig !== null) {
      sortedUsers.sort((a, b) => {
        if (a[sortConfig.key] < b[sortConfig.key]) {
          return sortConfig.direction === 'asc' ? -1 : 1;
        }
        if (a[sortConfig.key] > b[sortConfig.key]) {
          return sortConfig.direction === 'asc' ? 1 : -1;
        }
        return 0;
      });
    }

    return sortedUsers.filter(user =>
      (user.firstName.toLowerCase() + " " + user.lastName.toLowerCase()).includes(searchTerm.toLowerCase()) ||
      user.email.toLowerCase().includes(searchTerm.toLowerCase())
    );
  }, [users, searchTerm, sortConfig]);

  const requestSort = (key: keyof User) => {
    let direction: 'asc' | 'desc' = 'asc';
    if (sortConfig && sortConfig.key === key && sortConfig.direction === 'asc') {
      direction = 'desc';
    }
    setSortConfig({ key, direction });
  };
  
  const userColumns = [
    { key: 'avatar', label: 'Avatar' },
    { key: 'firstName', label: 'First Name', sortable: true },
    { key: 'lastName', label: 'Last Name', sortable: true },
    { key: 'email', label: 'Email', sortable: true },
    { key: 'phone', label: 'Phone' },
    { key: 'role', label: 'Role' },
    { key: 'status', label: 'Status' },
    { key: 'emailVerified', label: 'Verified' },
    { key: 'lastLogin', label: 'Last Login', sortable: true },
    { key: 'createdAt', label: 'Joined', sortable: true },
    { key: 'totalSpent', label: 'Total Spent', sortable: true },
    { key: 'credits', label: 'Credits', sortable: true },
    { key: 'actions', label: 'Actions' }
  ];

  return (
    <div className="container mx-auto px-4 py-8 bg-gray-50 min-h-screen">
      <div className="flex justify-between items-center mb-8">
        <h1 className="text-3xl font-bold text-gray-800">User Management</h1>
        <button className="bg-blue-600 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded-lg shadow-md hover:shadow-lg transition-all duration-200 ease-in-out flex items-center">
          <PlusIcon className="h-5 w-5 mr-2" />
          Add User
        </button>
      </div>

      <div className="bg-white rounded-xl shadow-lg p-6">
        <div className="flex items-center mb-4">
          <div className="relative w-full max-w-md">
            <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
              <MagnifyingGlassIcon className="h-5 w-5 text-gray-400" />
            </div>
            <input
              type="text"
              placeholder="Search by name or email..."
              className="w-full pl-10 pr-4 py-2 border border-gray-300 rounded-lg focus:ring-blue-500 focus:border-blue-500"
              onChange={(e) => setSearchTerm(e.target.value)}
            />
          </div>
        </div>

        <div className="overflow-x-auto">
          <table className="min-w-full divide-y divide-gray-200">
            <thead className="bg-gray-50">
              <tr>
                {userColumns.map((col) => (
                  <th 
                    key={col.key} 
                    scope="col" 
                    className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider"
                    onClick={() => col.sortable && requestSort(col.key as keyof User)}
                  >
                    <div className="flex items-center">
                      {col.label}
                      {col.sortable && (
                        <span className="ml-1">
                          {sortConfig?.key === col.key ? (
                            sortConfig.direction === 'asc' ? <ChevronUpIcon className="h-4 w-4" /> : <ChevronDownIcon className="h-4 w-4" />
                          ) : (
                            <ChevronUpIcon className="h-4 w-4 text-gray-300" />
                          )}
                        </span>
                      )}
                    </div>
                  </th>
                ))}
              </tr>
            </thead>
            <tbody className="bg-white divide-y divide-gray-200">
              {filteredAndSortedUsers.map((user) => (
                <tr key={user.id} className="hover:bg-gray-50">
                  <td className="px-6 py-4 whitespace-nowrap">
                    <img className="h-10 w-10 rounded-full" src={user.avatar} alt={`${user.firstName} ${user.lastName}`} />
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900">{user.firstName}</td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900">{user.lastName}</td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-600">{user.email}</td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-600">{user.phone}</td>
                  <td className="px-6 py-4 whitespace-nowrap"><RoleBadge role={user.role} /></td>
                  <td className="px-6 py-4 whitespace-nowrap"><StatusBadge status={user.status} /></td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-600 flex justify-center"><BooleanIndicator value={user.emailVerified} /></td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-600">{new Date(user.lastLogin).toLocaleString()}</td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-600">{new Date(user.createdAt).toLocaleDateString()}</td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-600 text-right">${user.totalSpent.toFixed(2)}</td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-600 text-right">{user.credits}</td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm font-medium">
                    <div className="flex items-center space-x-3">
                      <button className="text-blue-600 hover:text-blue-900"><PencilIcon className="h-5 w-5" /></button>
                      <button className="text-red-600 hover:text-red-900"><TrashIcon className="h-5 w-5" /></button>
                    </div>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>
    </div>
  )
}
